import React from "react"
import styled from "styled-components"

const IngredientsContainer = styled.div.attrs({
  className: "IngredientsContainer",
})`
  background: #dddddd;
  padding: 1em 2em;
  width: 70%;
  margin-left: auto;
`

// gets all ingredient information as props, no static queries here...
const Ingredients = ({ ingredients }) => {
  const names = ingredients.map(obj => obj.name).join(", ")
  return (
    <IngredientsContainer>
      <h3>Ingredients</h3>
      <div>{names}</div>
    </IngredientsContainer>
  )
}

export default Ingredients
